<template>
  <div class="container">
    <div class="row">
      <div class="col-1 col-xl-3"></div>
      <div class="col-10 col-xl-6">
        <b-form
          action="#"
          v-click-outside="onOutSideClick"
          :style="{ 'background-color': formColor}"
          class="noteForm ml-auto mr-auto mt-5"
          @click="onSideClick"
        >
          <b-input
            class="noteForm__input"
            placeholder="Название заметки"
            v-model="noteTitle"
            :style="{ 'background-color': formColor}"
          ></b-input>
          <b-form-group class="m-0">
            <b-form-textarea
              class="noteForm__input"
              v-model="noteText"
              placeholder="Заметка"
              :rows="2"
              :max-rows="6"
              v-if="showElement"
              :style="{ 'background-color': formColor}"
            ></b-form-textarea>
            <div class="row float-right editorButtons" key="row" v-if="showElement">
              <b-button-group size="sm" class="mx-1 mr-4 mb-2 float-right">
                <b-dropdown variant="link" dropup size="lg" no-caret>
                  <template slot="button-content">
                    <i class="fas fa-palette editorButtons__colorChange" key="i"></i>
                  </template>
                  <b-dropdown-header class="m-0 p-0 text-center">
                    <span v-for="(color, i) in colors" v-bind:key="i">
                      <input
                        type="radio"
                        name=""
                        :id="'colorRadio-'+i"
                        class="editorButtons__radioButton"
                      >
                      <label :for="'colorRadio-'+i" class="m-0 p-0">
                        <i
                          :class="colorChangeUncheckClass"
                          class="editorButtons__colorButton"
                          @click="changeFormColor(color)"
                          :style="{ 'color': color }"
                        ></i>
                      </label>
                      <br v-show="(i + 1) % 4 == 0">
                    </span>
                  </b-dropdown-header>
                </b-dropdown>
                <button
                  class="mr-0 mr-xl-4 float-right buttonSubmit"
                  @click.prevent="saveNote(noteTitle, noteText, formColor)"
                  key="button"
                  :disabled="noteText == ''"
                >Сохранить</button>
              </b-button-group>
            </div>
          </b-form-group>
        </b-form>
      </div>
      <div class="col-1 col-xl-3"></div>
    </div>
  </div>
</template>

<script>
import ClickOutside from "vue-click-outside";

export default {
  name: "AddNote",
  data: function() {
    return {
      noteText: "",
      noteTitle: "",
      noteColor: "red",
      showElement: false,
      colors: [
        "white",
        "#F28B82",
        "#FBBC04",
        "#FFF475",
        "#CCFF90",
        "#A7FFEB",
        "#CBF0F8",
        "#AECBFA",
        "#D7AEFB",
        "#FDCFE8",
        "#E6C9A8",
        "#E8EAED"
      ],
      formColor: "white",
      colorChangeUncheckClass: "fa fa-circle",
      colorChangeCheckClass: "far fa-check-circle"
    };
  },
  methods: {
    saveNote(noteTitle, noteText, noteColor) {
      if (noteTitle == "") {
        noteTitle = "Новая заметка";
      }
      this.formColor = "white";
      this.$emit("addNote", {
        noteText: noteText,
        noteTitle: noteTitle,
        noteColor: noteColor
      });
      this.noteText = "";
      this.noteTitle = "";
      //this.noteColor = "";
    },
    onSideClick() {
      if (this.showElement == false) {
        return (this.showElement = true);
      }
    },
    onOutSideClick() {
      this.formColor = "white";
      return (this.showElement = false);
    },
    changeFormColor(color) {
      this.formColor = color;
    }
  },
  directives: {
    ClickOutside
  }
};
</script>

<style lang="less" scoped>
.buttonSubmit {
  transition: all 0.5s;
  outline: none;
  border: 0px;
  font-weight: 600;
  border-radius: 10px;
  padding: 5px 20px 5px 20px;
  cursor: pointer;
  background-color: transparent;
}

.buttonSubmit:hover {
  transition: all 0.5s;
  outline: none;
  background-color: rgba(0, 0, 0, 0.1) !important;
  border: 0px;
  font-weight: 600;
}

.buttonSubmit:active {
  transition: all 0.5s;
  outline: none;
  background-color: rgba(0, 0, 0, 0.15) !important;
  border: 0px;
  font-weight: 600;
}

.noteForm {
  transition: all 0.6s;
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3),
    0 2px 6px 2px rgba(60, 64, 67, 0.15);
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}

.noteForm__input {
  transition: all 0.6s;
  color: black;
  border: 0px;
  resize: none;
  color: rgba(0, 0, 0, 0.8);
}

.noteForm__input:focus {
  -webkit-box-shadow: 0 0 0 0 !important;
  box-shadow: 0px 0px 0px rgba(255, 255, 255, 0.25) !important;
  color: black;
}

.editorButtons__colorChange {
  transition: all 0.5s;
  cursor: pointer;
  color: rgba(0, 0, 0, 0.5);
  margin: auto;
}

.editorButtons__colorChange:hover {
  transition: all ease-out 0.5s;
  color: black;
}

.editorButtons__colorChange:active {
  transition: all ease-out 0.1s;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}

.editorButtons__radioButton {
  display: none;
  position: absolute;
}

.editorButtons__colorButton {
  transition: all ease-out 0.2s;
  margin: 5px;
  font-size: 25px;
  cursor: pointer;
  position: relative;
  -webkit-text-stroke: 2px rgba(0, 0, 0, 0.2);
}

.editorButtons__colorButton:hover {
  transform: translate(0px, -1.5px);
  transition: all ease-out 0.2s;
  -webkit-text-stroke: 2px rgba(0, 0, 0, 0.6);
  text-shadow: 0px 0px 20px;
}
</style>
