<template>
  <div class="container">
    <div class="row mt-5">
      <transition name="fade">
        <div class="row text-center bgNoNotes" v-if="!notes.length"></div>
      </transition>
      <transition-group name="fade">
        <div
          v-for="(note, i) in notes"
          v-bind:key="'st:'+i"
          class="m-2 mt-3 noteCard"
          @click.self="openEditor(i, note.noteTitle, note.noteText, note.noteColor)"
          @mouseover.self="showDeleteBtn = true"
          @mouseleave.self="showDeleteBtn = false"
          :style="{'background-color': note.noteColor}"
        >
          <span class="noteCard__deleteBtn d-none d-lg-block d-md-block d-xl-block" @click="deleteNote(i)">
            <i class="fas fa-times"></i>
          </span>
          <h5 @click.self="openEditor(i, note.noteTitle, note.noteText, note.noteColor)">{{note.noteTitle}}</h5>
          <p @click.self="openEditor(i, note.noteTitle, note.noteText, note.noteColor)">{{note.noteText}}</p>
        </div>
      </transition-group>
    </div>
    <transition name="modal">
      <div class="modal-mask" v-if="showEditor">
        <div class="modal-wrapper">
          <div class="modal-container" v-click-outside="onOutSideClick" :style="{'background-color': currentColor}">
            <div class="modal-header">
              <slot name="header">
                <h3>{{currentTitle}}</h3>
              </slot>
            </div>
            <div class="modal-body">
              <slot name="body">
                <p>{{currentText}}</p>
              </slot>
            </div>
            <div class="modal-footer d-xl-none">
              <slot name="footer">
                <b-dropdown variant="link" dropup size="lg" no-caret >
                  <template slot="button-content" >
                    <b-button variant="outline-danger" size="sm">
                      <i class="far fa-trash-alt"></i>
                    </b-button>
                  </template>
                  <b-dropdown-header class="m-0 p-0 text-center">
                    <p>Удалить?</p>
                    <b-button
                      variant="outline-danger"
                      size="sm"
                      @click="showEditor = false, deleteNote(currentId)"
                    >Да</b-button>
                  </b-dropdown-header>
                </b-dropdown>
              </slot>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import ClickOutside from "vue-click-outside";

export default {
  name: "Notes",
  props: ["notes"],
  data: function() {
    return {
      currentId: "",
      currentTitle: "",
      currentText: "",
      currentColor: "",
      showEditor: false,
      showDeleteBtn: false
    };
  },
  methods: {
    openEditor(i, noteTitle, noteText, currentColor) {
      this.currentId = i;
      this.currentTitle = noteTitle;
      this.currentText = noteText;
      this.currentColor = currentColor;
      this.showEditor = true;
    },
    onOutSideClick() {
      return (this.showEditor = false);
    },
    deleteNote(i) {
      this.notes.splice(i, 1);
    }
  },
  directives: {
    ClickOutside
  }
};
</script>

<style lang="less" scoped>
.noteCard {
  cursor: pointer;
  display: inline-block;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3),
    0 2px 6px 2px rgba(60, 64, 67, 0.15);
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  border-radius: 10px;
}

.noteCard:hover {
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.5),
    0 2px 6px 4px rgba(60, 64, 67, 0.2);
}

.noteCard__deleteBtn {
  opacity: 0;
  float: right;
  transform: translate(30px, -15px);
  border: 1px solid;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  width: 25px;
  height: 25px;
  text-align: center;
  transition-duration: 0.2s;
}

.noteCard:hover .noteCard__deleteBtn {
  transition-duration: 0.2s;
  opacity: 1;
}

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 300px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3),
    0 2px 6px 2px rgba(60, 64, 67, 0.15);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
  border-radius: 10px;
}

.modal-header h3 {
  margin-top: 0;
  word-break: break-all;
}

.modal-body {
  margin: 20px 0;
}

.modal-body p {
  word-break: break-all;
}

.modal-default-button {
  float: right;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.bgNoNotes {
  background-image: url("../assets/noNotes2.png");
  background-size: 200px;
  background-repeat: no-repeat;
  background-position: center;
  height: 300px;
  width: 100%;
}
</style>
